import { Component, OnInit } from '@angular/core';
import { Global } from '../lib/global';
import { RestSvc } from '../lib/restSvc';
import { DialogService } from '../lib/dialog';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-lms-menu',
  templateUrl: './lms-menu.component.html',
  styleUrls: ['./lms-menu.component.scss']
})
export class LmsMenuComponent implements OnInit {

  

  constructor(private dlgSvc: DialogService, private restSvc: RestSvc, private _Activatedroute: ActivatedRoute) { }



 

  name = "";
  aboutMe = "";
  mobileNbr = "";
  dob = "";
  address = "";


  // Education
  edus = [];
  cboEdus = []
  edu = "";
  eduNote = "";
  eduTitle = "";
  eduSelected = "Add Education";
  showEduTitle = true;


  // Skills
  skills = [];
  cboSkills = []
  cboSkillRates = ["1 Star", "2 Stars", "3 Stars", "4 Stars", "5 Stars"];
  skillRate = "";
  skill = "";
  skillNote = "";
  skillTitle = "";
  skillSelected = "Add Skill";
  showSkillTitle = true;


  // Certs
  certs = [];
  cboCerts = []
  cert = "";
  certNote = "";
  certTitle = "";
  certSelected = "Add Skill";
  showCertTitle = true;



  // Acheivment
  acheives = [];
  cboAcheives = []
  acheive = "";
  acheiveNote = "";
  acheiveTitle = "";
  acheiveSelected = "Add Acheivement";
  attachAcheive = "" ;
  showAcheiveTitle = true;


  // Expereinces
  exps = [];
  cboExps = [];
  exp = "";
  expNote = "";
  expTitle = "";
  expSelected = "Add Education";

  // for Optimus Minds
  moeExpiry = "";
  moeFile = "" ;
  program = "" ;
  aiChk = false;
  atRiskChk = false;
  careerChk = false;
  dramaChk = false;
  danceChk = false;
  itChk = false;
  skillsChk = false;
  tongueChk = false;
  musicChk = false;
  outdoorChk = false;
  sportsChk = false;
  visualChk = false;

 // certFile = "" ;
  attachCert = "" ;

  newCert = false ;
  newEdu = false ;
  newSkill = false ;
  newExp = false ;
  newAcheive = false ;







  // End of Getters & Setters






  showExpTitle = true;


  



  ngOnInit(): void {




    switch (this._Activatedroute.snapshot.url.toString().toLowerCase()) {
      case "optimusminds":
       Global.init('optimusMinds');
        break;
        case "lms":
       Global.init('lms');
        break;
      default:
        Global.init('cvSalon');
        break;
    }


    let email = this._Activatedroute.snapshot.paramMap.get("email");


    if (email === null) {

      Global.dlgAuthMode = 1;

    
      this.openModal('dlgAuth');

    }
    else {

      this.restSvc.emailToken(email).then(
        (msg) => {

          let adminLevel = msg['adminLevel'];



          Global.email = email;
          Global.token = msg['token'];
          Global.pin = msg['pin'];
          Global._id = msg['_id'];
          Global.adminLevel = adminLevel;
          Global.dlgAuthMode = 2;

          this.openModal('dlgAuth');

        },
        (err) => {

          alert('Error: ' + JSON.stringify(err));

        })

    }



  }


  get mnu() {
    return Global.mnu ;
  }

  get clientName() {
    return Global.clientName;
  }

  get programs() {
    return Global.locData["programs"];
  }

  get stage() {

   
      return Global.locData["stage"] ;
    
  
  }

  openModal(id: string) {
  
    this.dlgSvc.open(id);
  }

// -
 onWelcome() {
    Global.mnu = -1 ;
  }

onAboutMe() {
  if (this.stage <= -1 ) {
    alert("Please view Welcome Page and select Next Step") ;
    return ;
  }
  Global.mnu = 0 ;
  Global.lmsMenuName = "aboutMe"
}


// Profile
onMyClassMates() {
  if (this.stage < 1 ) {
    alert("Please enter your informaton on About Yourself Page and select Next Step") ;
    return ;
  }

  this.restSvc.list().then(
  (msg) => {

    Global.lmsCards = msg ;
  

    
    // alert(JSON.stringify( Global.lmsCards))

  },
  (err) => {

    alert('Error: ' + JSON.stringify(err));

  })



 Global.mnu = 1 ;

}

onVideo() {

  if (this.stage <  2 ) {
    alert("Please view Class Participants and select Next Step") ;
    return ;
  }

  Global.mnu = 2 ;
}


onAssign1A() {

  if (this.stage <  3 ) {
    alert("Please watch Video and select Next Step") ;
    return ;
  }

  Global.mnu = 3 ;



}

onAssign1B() {
 

  if (this.stage <  4 ) {
    alert("Please complete Assignment 1A and select Next Step") ;
    return ;
  }

  Global.mnu = 4 ;

  this.restSvc.listUserStories().then(
    
    (msg) => {
  
   
      
      Global.lmsUserStories = msg ;

      

    },
    (err) => {
  
      alert('Error: ' + JSON.stringify(err));
  
    })
  


  
}

onFinal() {

  // if (this.stage <=  5 ) {
  //   alert("Please complete Assignment 1B and select Next Step") ;
  //   return ;
  // }

  if (this.stage <  5 ) {
    alert("Please complete Assignment 1B and select Next Step") ;
    return ;
  }

  Global.mnu = 5 ;
}

onClassChat() {
  Global.mnu = 6 ;
  
}

onFeedBack() {
  Global.mnu = 7 ;
}



}